import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'
// function mainContainer(r){
//    return require.ensure([],function(){
//      return r(require('@/components/main_container'));
//    })
// }
const mainContainer = r => require.ensure([], () => r(require('@/mainContainer')));
const Index = r => require.ensure([], () => r(require('@/components/index/Index')));

//登录模块
const Register = r => require.ensure([], () => r(require('@/components/login/Register')));
const Login = r => require.ensure([], () => r(require('@/components/login/Login')));

//校园风采
const Campus = r => require.ensure([], () => r(require('@/components/campus/Campus')));
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: 'main/index',
    },
    {
      path: '/main',
      name: 'main',
      redirect: 'main/index',
      component: mainContainer,
      children:[
        {
          path: 'index',
          name: 'Index',
          component: Index
        },
        {
          path: '/login/register',
          name: 'Register',
          component: Register
        },
        {
          path: '/login/login',
          name: 'Login',
          component: Login
        },
        {
          path: '/campus/campus',
          name: 'Campus',
          component: Campus
        }
      ]
    }
  ]
})



